import React from 'react'
import Link from 'gatsby-link'
import showcaseMp4 from './showcase.mp4'
import showcaseWebm from './showcase.webm'

const IndexPage = () => (
  <div>
    <video id='index-vid' preload={true} autoPlay={true} loop={true} muted={true}>
      <source src={showcaseMp4} type='video/mp4'/>
      <source src={showcaseWebm} type='video/mp4'/>
    </video>
    <main id='indexContent' className='mainContent'>
      <h2 className='page-title'>Marvin Cayetano</h2>
      <p>I make web and mobile apps.</p>
      <div className="index-social">
        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24"><path d="M336 96c21.2 0 41.3 8.4 56.5 23.5S416 154.8 416 176v160c0 21.2-8.4 41.3-23.5 56.5S357.2 416 336 416H176c-21.2 0-41.3-8.4-56.5-23.5S96 357.2 96 336V176c0-21.2 8.4-41.3 23.5-56.5S154.8 96 176 96h160m0-32H176c-61.6 0-112 50.4-112 112v160c0 61.6 50.4 112 112 112h160c61.6 0 112-50.4 112-112V176c0-61.6-50.4-112-112-112z"/><path d="M360 176c-13.3 0-24-10.7-24-24s10.7-24 24-24c13.2 0 24 10.7 24 24s-10.8 24-24 24zM256 192c35.3 0 64 28.7 64 64s-28.7 64-64 64-64-28.7-64-64 28.7-64 64-64m0-32c-53 0-96 43-96 96s43 96 96 96 96-43 96-96-43-96-96-96z"/></svg>
      </div>
    </main>
  </div>
)

export default IndexPage



